body {
    margin: 0;
    background-color: rgba(20, 20, 20, 1);
    color: white;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
      Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  }
  
  .yts {
    color: white;
  }
  
  .yts:hover {
    color: #effd4a;
  }
  
  .movie_detail {
    position: relative;
    top: 50%;
    bottom: 50%;
    width: 100%;
    height: 100vh;
    display: flex;
  }
  
  .detail_title {
    font-size: 30px;
    text-align: center;
    max-width: 400px;
    margin: 0 auto;
    margin-bottom: 20px;
  }
  
  .detail_img {
    width: 380px;
    margin: 0 auto;
    border-radius: 10px;
  }
  
  .detail_left {
    width: 40%;
    min-width: 400px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-right: 15px;
  }
  
  .detail_right {
    width: 60%;
    display: flex;
    justify-content: center;
    margin-top: 10px;
    flex-direction: column;
    max-width: 450px;
  }
  
  .detail_description {
    font-size: 18px;
    width: 50%;
    line-height: 1.5em;
    margin-bottom: 30px;
    color: #effd4a;
    min-width: 400px;
  }
  
  .movie_genres {
    font-size: 13px;
    font-weight: 500;
    letter-spacing: 0.1em;
    display: inline-block;
    margin-right: 10px;
    margin-bottom: 10px;
    padding: 10px;
    background: rgba(104, 228, 0, 0.9);
    color: #effd4a;
    border-radius: 10px;
  }
  
  .detail_detail {
    white-space: nowrap;
    display: flex;
    gap: 10px;
    width: 50%;
    justify-content: space-between;
  }
  
  .loading_detail {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 15px;
    color: rgba(104, 228, 0, 1);
    font-family: "Press Start 2P", cursive;
    line-height: 2.5em;
  }

  .watchButton {
    padding: 10px 20px;
    font-size: 16px;
    margin-top: 10px;
    color: red;
  }

  .imgStyle{
    box-shadow: -3px 6px 10px rgba(225, 223, 223, 0.851); 
    margin-right: 40px;
  }